.svg-sprite-ere-divisie::before {
  overflow: hidden;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 1000%;
  background-repeat:no-repeat;
}


.outer {
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 1 / 1 * 100%;
  }

  > .inner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}



.svg-sprite-ere-divisie {
  &.ajax::before {
    background-position: 1.3% 4.5%;
  }

  &.az::before {
    background-position: 15% 4.5%;
  }

  &.psv::before {
    background-position: 29% 4.5%;
  }

  &.fc-utrecht::before {
    background-position: 43% 4.5%;
  }

  &.vitesse::before {
    background-position: 57% 4.5%;
  }

  &.willem-ii::before {
    background-position: 71% 4.5%;
  }

  &.heracles::before {
    background-position: 84.5% 4.5%;
  }

  &.sc-heerenveen::before {
    background-position: 98.5% 4.5%;
  }

  &.fc-groningen::before {
    background-position: 1.5% 50%;
  }

  &.feyenoord::before {
    background-position: 15% 50%;
  }

  &.fc-twente::before {
    background-position: 29% 50%;
  }

  &.sparta::before {
    background-position: 43% 50%;
  }

  &.fc-emmen::before {
    background-position: 57% 50%;
  }

  &.fortuna-sittard::before {
    background-position: 71% 50%;
  }

  &.pec-zwolle::before {
    background-position: 84.5% 50%;
  }

  &.vvv-venlo::before {
    background-position: 98.6% 50%;
  }

  &.rkc::before {
    background-position: 1.5% 95%;
  }

  &.sc-heerenveen-flat::before {
    background-position: 15.2% 95%;
  }

  &.ere-divisie::before {
    background-position: 27.5% 111%;
    background-size: 600%;
  }
}
