.svg-sprite-kk-divisie::before {
  overflow: hidden;
  background-image: url(data:image/svg+xml;base64,);

  background-size: 1000%;
  background-repeat:no-repeat;
}


.outer {
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 1 / 1 * 100%;
  }

  > .inner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}

.svg-sprite-kk-divisie {
  &.almere-city-fc::before {
    background-position: 1.3% 4.5%;
  }

  &.de-graafschap::before {
    background-position: 15% 4.5%;
  }

