@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@300..700&display=swap");
.breakpoint.sticky-content {
  top: 120px;
  z-index: 101;
}

@media only screen and (max-width: 1023px) {
  .breakpoint::after {
    content: 'medium-only';
  }
}

@media only screen and (min-width: 1024px) {
  .breakpoint::after {
    content: 'medium-up';
  }
}

@media only screen and (min-width: 1200px) {
  .breakpoint::after {
    content: 'large-up';
  }
}

@media only screen and (min-width: 1600px) {
  .breakpoint::after {
    content: 'xlarge-up';
  }
}

@media only screen and (max-width: 767px) {
  .breakpoint::after {
    content: 'small-only';
  }
}

html {
  --light-text-color: rgb(72, 72, 74);
  --link-color: rgb(0, 112, 201);
  --main-background-color: rgb(255, 255, 255);
  --main-text-color: rgb(51, 51, 51);
  --main-list-color: rgb(51, 51, 51);
}

@media (prefers-color-scheme: dark) {
  html {
    --light-text-color: rgb(199, 199, 204);
    --link-color: rgb( 100, 210, 255);
    --main-background-color: rgb(28, 28, 30);
    --main-text-color: rgb(229, 229, 234);
    --main-list-color: rgb(51, 51, 51);
  }
}

html, html[data-theme="light"] {
  --light-text-color: rgb(72, 72, 74);
  --link-color: rgb(0, 112, 201);
  --main-background-color: rgb(255, 255, 255);
  --main-text-color: rgb(51, 51, 51);
}

html[data-theme="dark"] {
  --light-text-color: rgb(199, 199, 204);
  --link-color: rgb( 100, 210, 255);
  --main-background-color: rgb(28, 28, 30);
  --main-text-color: rgb(229, 229, 234);
  --main-list-color: rgb(51, 51, 51);
}

@media (prefers-color-scheme: dark) {
  html, html[data-theme="dark"] {
    --light-text-color: rgb(199, 199, 204);
    --link-color: rgb( 100, 210, 255);
    --main-background-color: rgb(28, 28, 30);
    --main-text-color: rgb(229, 229, 234);
    --main-list-color: rgb(51, 51, 51);
  }
  html[data-theme="light"] {
    --light-text-color: rgb(72, 72, 74);
    --link-color: rgb(0, 112, 201);
    --main-background-color: rgb(255, 255, 255);
    --main-text-color: rgb(51, 51, 51);
  }
}

:root {
  --global-background: hsl(0 0% 100%);
  --global-foreground: hsl(0 0% 0%);
  --accent-color: hsl(226deg 100% 50%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --global-background: hsl(0 0% 0%);
    --global-foreground: hsl(0 0% 100%);
    --accent-color: hsl(226deg 100% 50%);
  }
}

html {
  background-color: var(--global-background);
  color: var(--global-foreground);
  transition: all 200ms;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.theme-dark {
  --text-color: #eeeeee;
  --btn-color: #111111;
  --background-color: #111111;
}

.theme-light {
  --text-color: #111111;
  --btn-color: #eeeeee;
  --background-color: #eeeeee;
}

html {
  font-size: 1rem;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

body {
  font-family: "Fira Code", monospace;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  min-height: 100vh;
  text-align: left;
  color: var(--main-text-color);
  background-color: var(--main-background-color);
  transition: background-color 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
}

strong {
  font-family: "Fira Code", monospace;
  font-weight: 700;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Fira Code", monospace;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
  margin: 0;
  padding: 0;
}

h1 {
  font-weight: 200;
  font-size: 2.625rem;
}

h1.title {
  margin: 20px 0 20px 0;
  padding: 0 0 10px 0;
  color: var(--main-text-color);
  width: 100%;
  transition: color 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
}

h1.form-title {
  margin: 20px 0 20px 0;
  padding: 0 0 10px 0;
  color: var(--main-text-color);
  width: 100%;
  transition: color 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
  transition: all 0.22s cubic-bezier(0.345, 0, 0.25, 1) 0s;
}

@media only screen and (min-width: 768px) {
  h1.form-title {
    margin: 40px 0 20px 0;
    padding: 0 0 20px 0;
  }
}

h2 {
  font-weight: 200;
  font-size: 1.125rem;
}

h2.title {
  margin: 20px 0 20px 0;
  padding: 0 0 10px 0;
  border-bottom: solid 1px var(--main-text-color);
  color: var(--main-text-color);
  width: 100%;
  transition: color 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
}

@media only screen and (min-width: 768px) {
  h2.title {
    margin: 40px 0 40px 0;
    padding: 0 0 20px 0;
  }
}

@media only screen and (min-width: 768px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-weight: 200;
  font-size: 1.125rem;
}

h3.title {
  width: 100%;
  transition: all 0.22s cubic-bezier(0.345, 0, 0.25, 1) 0s;
}

@media only screen and (min-width: 768px) {
  h3 {
    font-size: 1.375rem;
  }
}

p {
  margin: 0;
  padding: 0;
}

header, nav, .breakpoint, .site-content, footer {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 1em;
  position: relative;
}

@media only screen and (min-width: 768px) {
  header, nav, .breakpoint, .site-content, footer {
    padding: 1.5em;
  }
}

.wrapper-title {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  color: #eeeeee;
  margin-bottom: 1em;
  text-align: center;
}

.wrapper-title:last-child {
  margin-bottom: 0;
}

@media only screen and (min-width: 768px) {
  .wrapper-title {
    flex-direction: row;
    text-align: left;
  }
}

.site-content {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;
  padding: 0 0;
}

.site-content .site-container {
  padding: 10px 10px;
}

@media only screen and (min-width: 768px) {
  .site-content .site-container {
    padding: 20px 20px;
  }
}

.sticky-content {
  position: sticky;
  top: 64px;
  z-index: 100;
  display: block;
}

section {
  padding: 20px 60px;
}

.theme-selector {
  display: none;
}

@supports (--a: 0) {
  .theme-selector {
    display: block;
  }
}

.darkmode-toggle {
  right: 14px;
  bottom: 14px;
  left: unset;
  width: 32px;
  height: 32px;
  z-index: 99999;
}

@media only screen and (min-width: 768px) {
  .darkmode-toggle {
    right: 28px;
    bottom: 18px;
    width: 42px;
    height: 42px;
  }
}

.darkmode--activated h2.title,
.darkmode--activated .club-name {
  color: #eeeeee;
}

.darkmode--activated h2.title {
  border-bottom: solid 1px #eeeeee;
}

.darkmode--activated .a-better-select .custom-select-options li {
  color: #eeeeee;
  background-color: #111111;
  border-bottom: solid 1px #eeeeee;
}

.darkmode--activated .a-better-select .select-css {
  color: #eeeeee;
  background-color: #111111;
}

.darkmode--activated .a-better-select .custom-select::before {
  background-image: url(data:image/svg+xml;base64,);
}

.darkmode--activated .hex-grid__content {
  background-color: #111111;
  transition: all 0.33s cubic-bezier(0.345, 0, 0.25, 1) 0s;
}

.device-row {
  width: 100%;
  max-width: 1440px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.mobile {
  display: block;
  padding: 1vw;
}

.phone.portrait {
  position: relative;
  border: 10px solid #222;
  border-radius: 30px;
  min-width: 375px;
}

.phone.portrait:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 216.533333333%;
}

.phone.portrait > .inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.phone.landscape {
  position: relative;
  border: 10px solid #222;
  border-radius: 30px;
  min-width: 812px;
}

.phone.landscape:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 46.1822660099%;
}

.phone.landscape > .inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.tablet.portrait {
  position: relative;
  width: auto;
  height: 0;
  padding-top: 133.3984375%;
  border: 10px solid #222;
  border-radius: 30px;
}

.tablet.portrait > .inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.tablet.landscape {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 74.9633967789%;
  border: 10px solid #222;
  border-radius: 30px;
}

.tablet.landscape > .inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.dark-mode-switch {
  position: relative;
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin-left: 1rem;
}

.dark-mode-switch .icon {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  transition: opacity 0.2s cubic-bezier(0.2, 0.8, 0.2, 1) 0s, transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.2) 0s;
}

.dark-mode-switch .visible {
  opacity: 1;
  transform: rotate(360deg);
  border-radius: 50%;
}

.dark-mode-switch .invisible {
  opacity: 0;
}

.dark-mode-switch .sun {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
}

.dark-mode-switch .moon {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
}

.switch-thingyy {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

@media only screen and (min-width: 768px) {
  .switch-thingyy {
    width: 25%;
  }
}

/* Avoid Chrome to see Safari hack */
@supports (-webkit-touch-callout: none) {
  body {
    /* The hack for Safari */
    height: -webkit-fill-available;
  }
}

.hex-grid {
  display: flex;
  justify-content: center;
}

.hex-grid__list {
  --amount: 5;
  position: relative;
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(var(--amount), 1fr 2fr) 1fr;
  gap: 2.5rem 5rem;
}

.hex-grid__list.only-one {
  gap: 0rem 5rem;
}

.hex-grid__item {
  position: relative;
  grid-column: 1 / span 3;
  grid-row: calc(var(--counter) + var(--counter))/span 2;
  filter: drop-shadow(0 0 10px rgba(68, 68, 68, 0.08));
  height: 0;
  padding-bottom: 90%;
  text-align: center;
  justify-items: center;
  position: relative;
}

.hex-grid__item .club-name {
  position: absolute;
  bottom: .1rem;
  width: 100%;
  text-align: center;
  font-size: 0.5625rem;
  font-weight: 300;
  color: var(--main-list-color);
  white-space: nowrap;
}

@media only screen and (min-width: 768px) {
  .hex-grid__item .club-name {
    font-size: 0.875rem;
    bottom: .5rem;
    font-weight: 600;
  }
}

.hex-grid__content {
  position: absolute;
  height: 100%;
  width: 100%;
  font-size: 1.125rem;
  color: #111111;
  background-color: #eeeeee;
  clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  text-align: center;
  transition: all 0.33s cubic-bezier(0.345, 0, 0.25, 1) 0s;
}

